/* eslint-disable import/prefer-default-export */
import { ethers } from "ethers";

import AuctionRegistrarAbi from '../contracts/AuctionRegistrar.json';

const AuctionRegistrarAddress = "0xB0815F5eE373bE63Bd5a91900F752546bb2f15C8";

export const getContractHash = async (connector) => {
  if (!connector) return ;
  const walletProvider = connector;
  let nftContract = new ethers.Contract(
    AuctionRegistrarAddress,
    AuctionRegistrarAbi,
    walletProvider
  );
  return nftContract;
};
